'use client';

import Image from 'next/image';
import Link from 'next/link';
import { motion } from 'framer-motion';
import { Users, Globe, Target, Building2, ChevronRight } from 'lucide-react';

export default function AboutSLCRPage() {
  return (
    <div className="min-h-screen">
      {/* Hero Banner */}
      <section className="relative py-20 bg-gradient-to-r from-primary via-primary-light to-secondary">
        <div className="max-w-7xl mx-auto px-4 text-center text-white">
          <motion.h1
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="text-4xl md:text-5xl font-bold mb-4"
          >
            About SLCR
          </motion.h1>
          <p className="text-lg text-white/80">Smart Laboratory on Clean Rivers</p>
        </div>
      </section>

      {/* Main Content */}
      <section className="py-16 px-4 bg-white">
        <div className="max-w-5xl mx-auto">
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ delay: 0.2 }}
          >
            <p className="text-lg text-gray-700 leading-relaxed mb-8">
              Smart Laboratory on Clean Rivers in Varanasi (SLCR) is the joint initiative by the
              Hon'ble Prime Minister of India, H.E. Shri. Narendra Modi and the Prime Minister of
              Denmark, H.E. Ms Mette Frederiksen. A Memorandum of Understanding (MoU) was signed
              between the Ministry of Jal Shakti and the Danish Environment Ministry as a broad-based
              framework in the field of Water Resources Development and Management including the
              SLCR initiative.
            </p>

            <h2 className="text-2xl font-bold text-primary mb-4">
              Concept of Smart Living Lab on Clean River (SLCR) - Small River Rejuvenation
            </h2>
            <p className="text-gray-700 leading-relaxed mb-8">
              SLCR is more than a laboratory; it is an ecosystem of learning, engagement, co-creation,
              and experimentation in the real environment. SLCR consists of two major components:
            </p>

            <div className="grid md:grid-cols-2 gap-6 mb-12">
              <div className="card p-6 border border-gray-100">
                <h3 className="text-xl font-bold text-primary mb-3">Hybrid Lab (Virtual + Secretariat)</h3>
                <p className="text-gray-600 text-sm">
                  Provides the platform to bring all Govt. Dept., Stakeholders, Academician, Industries
                  and Community to engage to evaluate the Global sustainable solutions (technologies,
                  Policies, frameworks) and enable their implementation in local context through
                  Engagement, Learning and Co-creation. The Hybrid lab will also enable the upscaling
                  of the solutions to the basin level and other rivers.
                </p>
              </div>
              <div className="card p-6 border border-gray-100">
                <h3 className="text-xl font-bold text-primary mb-3">On-field Living Lab</h3>
                <p className="text-gray-600 text-sm">
                  Having the real field area as an experimental site and is used to demonstrate
                  the co-creation on the field.
                </p>
              </div>
            </div>
          </motion.div>
        </div>
      </section>

      {/* Major Objectives */}
      <section className="py-16 px-4 bg-gray-50">
        <div className="max-w-5xl mx-auto">
          <h2 className="text-3xl font-bold text-primary mb-8 text-center">Major Objectives of SLCR</h2>
          <div className="grid md:grid-cols-2 gap-6">
            {[
              {
                icon: Users,
                text: 'Create a platform between Government authorities, Knowledge institutions, technology providers and citizens for knowledge sharing and co-creation to achieve clean river water.'
              },
              {
                icon: Globe,
                text: 'To bring the global solutions on current challenges in the field of clean river water and conduct research and development to fit in real environment through Living lab approach to make them scalable and economically attractive.'
              },
              {
                icon: Target,
                text: 'To Support NMCG in achieving its vision by focusing on small rivers.'
              },
              {
                icon: Building2,
                text: 'Develop the repository of all collected knowledge and technologies, which can be shared through various initiatives like River Cities Alliance (RCA)/Global River Cities Alliance (GRAC) to perform rejuvenation work on other small rivers or tributaries in India and in GRCA member countries.'
              }
            ].map((obj, index) => (
              <motion.div
                key={index}
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ delay: index * 0.1 }}
                className="card p-6 flex gap-4"
              >
                <div className="w-12 h-12 bg-primary/10 rounded-xl flex items-center justify-center shrink-0">
                  <obj.icon className="w-6 h-6 text-primary" />
                </div>
                <p className="text-gray-600 text-sm">{obj.text}</p>
              </motion.div>
            ))}
          </div>
          <p className="text-center text-gray-600 mt-8">
            The main outcomes of the SLCR will be successful demonstration of global solutions in
            the local context, which have a sound business model and can be upscaled to other
            small rivers/tributaries.
          </p>
        </div>
      </section>

      {/* Structure of SLCR */}
      <section className="py-16 px-4 bg-white">
        <div className="max-w-5xl mx-auto">
          <h2 className="text-3xl font-bold text-primary mb-8 text-center">Structure of SLCR</h2>
          <div className="space-y-6">
            <div className="card p-6 border-l-4 border-primary">
              <h3 className="text-xl font-bold text-primary mb-2">Joint Steering Committee</h3>
              <p className="text-gray-600 text-sm">
                National level committee with high-level representatives from both Denmark and India.
                To provide strategic guidance and review the progress activities which are identified
                by the Multi Stake Holder Working Group. To explore and secure funding for new projects,
                if necessary.
              </p>
            </div>
            <div className="card p-6 border-l-4 border-accent">
              <h3 className="text-xl font-bold text-primary mb-2">Project Review Committee</h3>
              <p className="text-gray-600 text-sm">
                To review the progress of the ongoing projects to assist the JSC. Quality control of the
                proposed new projects by the Multistakeholder Working group and share recommendation with
                the JSC. The PRC can request for a JSC meeting if new projects are to be reapproved or
                if they need strategic direction.
              </p>
            </div>
            <div className="card p-6 border-l-4 border-secondary">
              <h3 className="text-xl font-bold text-primary mb-2">The Multi Stakeholder Working Group</h3>
              <p className="text-gray-600 text-sm">
                It is the platform to bring the different Central and State Government agencies, Urban
                Local Bodies (ULBs), District Administrations and state and central missions that are
                relevant for the Varuna. Share the challenges in their respective areas to SLCR. Inform
                the ongoing activities in their respective areas to SLCR. Establish synergy and convergence
                with the other schemes/initiatives. Give strategic inputs if any during framing of the
                individual projects to SLCR.
              </p>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}
